   .main {
    padding: 10px;
    height: 100vh;
}

.layout {
    display: flex;
}

.sidebar {
    min-height: 100%;
    width: 300px;
    border-radius: 5px;
    background-color: #34495E; /* Dark blue background color */
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.5); /* Slightly transparent gray shadow */
    margin-right: 20px;
    color: white;
}

.content {
    width: 100%;
    height: 100%;
}

.header {
    height: 10vh;
    margin-bottom: 20px;
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.5); /* Slightly transparent gray shadow */
    background-color: #ECF0F1; /* Light gray background color */
}

.body {
    height: 85vh;
    margin-bottom: 20px;
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.5); /* Slightly transparent gray shadow */
    background-color: #ECF0F1; /* Light gray background color */
}

.logo h6 {
    font-size: 1.5rem;
    text-align: center;
    margin: 20px 0px;
}

.menu {
    margin-top: 50px;
}

.menu-item {
    margin-top: 30px;
}

.menu-item a {
    color: #FFFFFF; /* White text color */
    text-decoration: none;
    font-size: 1.2rem;
}

.menu-item i {
    font-size: 1.2rem;
    margin: 0 15px;
}

.active {
    background-color: #FFFFFF; /* White background color */
    padding: 10px;
}

.active a,
.active i {
    color: #34495E; /* Dark blue text color */
}

.header-content {
    display: flex;
    align-items: center;
    height: 50px;
    justify-content: flex-end;
    margin: 0 15px;
}

.header-content i {
    margin-right: 20px;
    font-size: 1.2rem;
}

.header-content a {
    text-decoration: none;
    font-size: 1.2rem;
    color: #3498DB; /* Blue text color */
    margin: 0 10px;
    text-transform: uppercase;
}
